import React, { useState } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import Navbar from "./Components/Navbar/Navbar";
import LearnHow from "./Components/LearnHow/LearnHow";
import Sidebar from "./Components/Sidebar/Sidebar";
import LockesAndToken from "./Pages/Lockers/Token";
import { Routes, Route } from "react-router-dom";
import styled from "styled-components";
import TokenLocker from "./Pages/Lockers/TokenLocker/TokenLockers";
import AppLabToken from "./Pages/AppLabToken/AppLabToken";

import Web3 from "web3";

import lockerContractAbi from './abis/LockerContractDAPPabi.json';

function App() {
  const [collapsed, setCollapsed] = useState(false);
  const [lockerContract, setLockerContract] = useState(null);
  const [connected, setConnected] = useState(false);
  const [account, setAccount] = useState(null);

  const ConnectAccount = async()=>{
    await loadWeb3();
    await loadBlockChatData();
  };
  
  const loadWeb3 = async()=>{
    if (window.ethereum) {
      window.web3 = new Web3(window.ethereum);
      await window.ethereum.enable();
  
    } else if (window.web3) {
      window.web3 = new Web3(window.web3.currentProvider);
    } else {
      window.alert(
        "Non-Ethereum browser detected. You should consider trying MetaMask!"
      );
    }
  }
  
  const loadBlockChatData = async()=>{
    const web3 = window.web3;
    const accounts = await web3.eth.getAccounts();
    web3.eth.defaultAccount = accounts[0];
    setAccount(accounts[0]);
  
    const networkId = await web3.eth.net.getId();
    if (networkId === 56  || networkId === 97) {
      const networkAddress = '0x8cF6a826521D8af780275C3b61E6Abf2530b4bAA';
      const lockerContract = await new web3.eth.Contract(lockerContractAbi.abi, networkAddress);
      setConnected(true);
      setLockerContract(lockerContract);

    } else {
      window.alert("Blockchat contract not deployed to detected network.");
      switchToBinanceTestNet();
    }
  }
  
  const switchToBinanceTestNet = async()=>{
    try {
      await window.ethereum.request({
        method: "wallet_switchEthereumChain",
        params: [{ chainId: "0x61" }],
      });
      loadBlockChatData();
    } catch (error) {
      if (error.code === 4902) {
        try {
          await window.ethereum.request({
            method: "wallet_addEthereumChain",
            params: [
              {
                chainId: "0x61",
                chainName: "Binance Smart chain TestNet",
                rpcUrls: ["https://data-seed-prebsc-1-s1.binance.org:8545/"],
                nativeCurrency: {
                  name: "Binance",
                  symbol: "BNB",
                  decimals: 18,
                },
                blockExplorerUrls: ["https://testnet.bscscan.com/"],
              },
            ],
          });
          loadBlockChatData();

        } catch (error) {
          console.log(error.message);
        }
      }
    }
  }





  return (
    <Wrapper>
      <div
        className="sidebar-container"
        style={{
          height: "100vh",
        }}
      >
        <Sidebar collapsed={collapsed} setCollapsed={setCollapsed} />
      </div>
      <div
        className="w-100 left-container"
        style={{
          background: "#FAFAFA",
          height: "100vh",
          overflowY: "scroll",
        }}
      >
        <Navbar connected={connected} account={account} connectAccount={ConnectAccount} collapsed={collapsed} />
        <LearnHow collapsed={collapsed} />
        <div style={{ overflowY: "scroll" }}>
          <Routes>
            <Route
              path="/tokenlocker"
              element={
                <TokenLocker
                  collapsed={collapsed}
                  title="View Liquidity Lockers"
                  connected={connected} account={connected} lockerContract={lockerContract}
                />
              }
            ></Route>
            <Route
              path="/token"
              element={
                <TokenLocker
                  collapsed={collapsed}
                  title="View Token Lockers"
                  connected={connected} account={account} lockerContract={lockerContract}
                />
              }
            ></Route>
            <Route
              path="/lockerstoken"
              element={<LockesAndToken connected={connected} account={account} lockerContract={lockerContract} collapsed={collapsed} />}
            ></Route>
            <Route
              path="/applabtocken"
              element={<AppLabToken collapsed={collapsed} />}
            ></Route>
          </Routes>
        </div>
      </div>
    </Wrapper>
  );
}

export default App;


const Wrapper = styled.div`
  display: flex;
  .sidebar-container {
    background: #0037c6 !important;
  }
  .left-container {
    scrollbar-width: none; /* for Firefox */
    overflow-y: scroll;
    background: #e5e5e5;
  }
  .left-container ::-webkit-scrollbar {
    width: 0px;
  }
`;
