import React from "react";
import styled from "styled-components";

import { BiSearch } from "react-icons/bi";

import MySelect from "../Select/Select";
import network from "../../images/network.svg";
import metamask from "../../images/metamask.svg";

const Wrapper = styled.div`
  height: 60px;

  background: rgba(1, 88, 211, 0.04);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 35px;
  box-shadow: 0 0 2px 0px #838383;
  border-bottom:.2px solid rgba(0,0,0,.05);
  .input-container {
    display: flex;

    align-items: center;
    padding: 0 14px;
    border-radius: 30px;
    background: #fff;
    width: 250px;
    border: 0.03px solid rgba(131, 131, 131, 0.2);
  }
  .input-container input {
    font-family: "Open Sans", sans-serif 
    border-radius: 30px;
    padding: 7px 0;
    border: 0;
    outline: 0;
    width: 100%;
    font-size: 15px;
    padding-left: 15px;
    color: #8b8b8b;
  }
  .search {
    cursor: pointer;
    font-size: 20px;
    color: #8190a6;
  }

  .select-container {
    display: flex;
  }
  .logo {
    display: none;
  }
  @media only screen and (max-width: 1199px) {
    .input-container {
      width: 180px;
    }
    .input-container input {
      padding: 8px 0;
    }
    padding: 0 15px;
  }

  @media only screen and (max-width: 991px) {
    justify-content: flex-end;
    .logo {
      width: 120px;
      height: 120px;
      display: block;
      display: none;
    }
    .input-container {
      display: none;
    }
  }
  @media only screen and (max-width: 767px) {
    justify-content: center;
  }
  @media only screen and (max-width: 614px) {
    justify-content: flex-end;
  }
  @media only screen and (max-width: 520px) {
    padding: 0;
    .logo {
      width: 60px;
      height: 60px;
      display: none;
    }
  }
`;

const Navbar = ({ collapsed,connected,account,connectAccount }) => {
  const networkSelect = ["First Sample", "Second Sample"];
  const metaMask = ["Connect"];
  return (
    <>
      <Wrapper className="d-none d-lg-flex">
        {collapsed && <img src="./images/logo.svg" alt="" className="logo" />}
        <div className="input-container">
          <BiSearch className="search" />
          <input type="text" placeholder="Search token" />
        </div>
        <div className="select-container">
          {" "}
          <MySelect
            selectArray={networkSelect}
            initialValue="Switch Network"
            image={network}
          />
          <MySelect connected={connected} account={account} connectAccount={connectAccount} selectArray={metaMask} image={metamask} />
        </div>
      </Wrapper>
      <Wrapper
        className="d-lg-none"
        style={{ justifyContent: !collapsed && "flex-end" }}
      >
        {collapsed && <img src="./images/logo.svg" alt="" className="logo" />}
        <div className="input-container">
          <input type="text" placeholder="Search token" />
          <BiSearch className="search" />
        </div>
        <div className="select-container">
          {" "}
          <MySelect
            selectArray={networkSelect}
            initialValue="Switch Network"
            image={network}
          />
          <MySelect selectArray={metaMask} image="./images/metamask.svg" />
        </div>
      </Wrapper>
    </>
  );
};
export default Navbar;
